(function() {
  'use strict';

  angular
    .module('demoApp')
    .controller('NumberDemoController', NumberDemoController);

  /** @ngInject */
  function NumberDemoController(demoUtilSvc) {
    var vm = this;

    vm.theValue = demoUtilSvc.getRandomDecimal(100.00, 900.00);
    vm.changeTheValue = function() {
      vm.theValue = demoUtilSvc.getRandomDecimal(100.00, 900.00);
    }

    vm.theOtherValue = 12345.67;

    vm.operations = [{
      label: 'Add',
      op: 'add'
    }, {
      label: 'Substract',
      op: 'sub'
    }, {
      label: 'Multiply',
      op: 'mul'
    }];

    vm.changeValue = function() {
      switch (vm.chosenOperation) {
        case 'add':
          vm.theOtherValue = vm.theOtherValue + vm.delta;
          break;
        case 'sub':
          vm.theOtherValue = vm.theOtherValue - vm.delta;
          break;
        case 'mul':
          vm.theOtherValue = vm.theOtherValue * vm.delta;
          break;
      }
    };

    vm.rollConfig = {
      rollBetween: true
    };

  }

})();

(function() {
  'use strict';

  angular
    .module('demoApp')
    .controller('IntroController', IntroController);

  /** @ngInject */
  function IntroController($scope, $timeout, demoUtilSvc) {
    var vm = this;
    vm.lower = 10000000;
    vm.upper = 50000000;
    vm.time = 1000;
    vm.theValue = demoUtilSvc.getRandomInt(vm.lower, vm.upper);

    var tm;
    var onTimer = function() {
      vm.theValue = demoUtilSvc.getRandomInt(vm.lower, vm.upper);
      vm.time = demoUtilSvc.getRandomInt(2000, 3000);
      tm = $timeout(onTimer, vm.time);
    };
    tm = $timeout(onTimer, vm.time);

    $scope.$on('$destroy', function() {
      if (tm) {
        $timeout.cancel(tm);
      }
    });
  }

})();

(function() {
  'use strict';

  angular
    .module('demoApp')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider) {
    $stateProvider
      .state('main.currency', {
        templateUrl: 'app/main/views/currency/currency.html',
        controller: 'CurrencyDemoController',
        controllerAs: 'currency'
      }).state('main.currency.case', {
        url: '/currency/case',
        templateUrl: 'app/main/views/currency/views/case/case.html',
        controller: 'CasesController',
        controllerAs: 'case'
      }).state('main.currency.cpu', {
        url: '/currency/cpu',
        templateUrl: 'app/main/views/currency/views/cpu/cpu.html',
        controller: 'CPUsController',
        controllerAs: 'cpu'
      }).state('main.currency.mem', {
        url: '/currency/mem',
        templateUrl: 'app/main/views/currency/views/mem/mem.html',
        controller: 'MemController',
        controllerAs: 'mem'
      }).state('main.currency.storage', {
        url: '/currency/storage',
        templateUrl: 'app/main/views/currency/views/storage/storage.html',
        controller: 'StorageController',
        controllerAs: 'storage'
      }).state('main.currency.cart', {
        url: '/currency/cart',
        templateUrl: 'app/main/views/currency/views/cart/cart.html',
        controller: 'CartController',
        controllerAs: 'cart'
      });
  }

})();

(function() {
  'use strict';

  angular
    .module('demoApp.currency')
    .factory('currencySvc', function(demoUtilSvc) {
      var svc = {};

      svc.cart = [];
      svc.totalPrice = 0;

      svc.addToCart = function(item) {
        svc.cart.push(item);
        calcTotalPrice(item, true);
      };

      svc.removeFromCart = function(item) {
        var inx = svc.cart.indexOf(item);
        svc.cart.splice(inx, 1);
        calcTotalPrice(item);
      };

      var calcTotalPrice = function(item, add) {
        if (add) {
          svc.totalPrice += item.price;
        } else {
          svc.totalPrice -= item.price;
        }
      };

      svc.foo = function() {
        return demoUtilSvc.getRandomInt(2, 33);
      }

      return svc;
    });

})();

(function() {
  'use strict';

  angular
    .module('demoApp')
    .controller('CurrencyDemoController', CurrencyDemoController);

  /** @ngInject */
  function CurrencyDemoController($state, currencySvc) {
    var vm = this;

    vm.actions = [{
      title: 'Cases',
      stateName: 'main.currency.case',
      icon: 'work'
    }, {
      title: 'CPUs',
      stateName: 'main.currency.cpu',
      icon: 'select_all'
    }, {
      title: 'Memory',
      stateName: 'main.currency.mem',
      icon: 'sd_card'
    }, {
      title: 'Hard-Drives',
      stateName: 'main.currency.storage',
      icon: 'storage'
    }];

    var setSelectedButton = function() {
      if ($state.current.name === 'main.currency.cart') {
        vm.selectedButton = vm.actions.length;
        return;
      }
      angular.forEach(vm.actions, function(item, inx) {
        if (item.stateName === $state.current.name) {
          vm.selectedButton = inx;
          return;
        }
      });
    };
    setSelectedButton();

    vm.svc = currencySvc;
    vm.rollConfig = {
      filter: 'currency'
    };

    vm.openMenu = function($mdOpenMenu, ev) {
      $mdOpenMenu(ev);
    };

    vm.navFromMenu = function(stateName) {
      $state.go(stateName);
    };

  }

})();

(function() {
  'use strict';

  angular
    .module('demoApp')
    .controller('DateDemoController', DateDemoController);

  /** @ngInject */
  function DateDemoController($scope, $timeout) {
    var vm = this;

    vm.theValue = new Date();

    vm.operations = [{
      label: 'Add',
      op: 'add'
    }, {
      label: 'Substract',
      op: 'sub'
    }];
    vm.chosenOperation = vm.operations[0].op;

    vm.changeValue = function() {
      var val = new Date(vm.theValue.valueOf());
      switch (vm.chosenOperation) {
        case 'add':
          val.setDate(vm.theValue.getDate() + vm.delta);
          break;
        case 'sub':
          val.setDate(vm.theValue.getDate() - vm.delta);
          break;
      }
      vm.theValue = val;
    };

    vm.resetValue = function() {
      vm.theValue = new Date();
    };

    vm.rollConfig = {
      filter: 'date',
      filterParam1: 'MMMM dd, yyyy',
      rollBetween: true
    };

    vm.triggerChange = function() {
      var preValue = vm.theValue;
      vm.theValue = 9999;
      vm.changeTimer = $timeout(function() {
        vm.theValue = preValue;
      }, 250);
    };

    $scope.$on('$destroy', function() {
      if (vm.changeTimer) {
        $timeout.cancel(vm.changeTimer);
      }
    });

  }

})();

(function() {
  'use strict';

  angular
    .module('demoApp')
    .factory('demoUtilSvc', function() {
      var svc = {};

      svc.getRandomInt = function(min, max) {
        return parseInt((Math.random() * (max - min) + min));
      };

      svc.getRandomDecimal = function(min, max) {
        return parseFloat((Math.random() * (max - min) + min).toFixed(2));
      };

      svc.getRandomDate = function() {
        var inc = parseInt(svc.getRandomDecimal(1, 99));
        var date = new Date();
        date.setDate(date.getDate() + inc);
        return date;
      };

      return svc;
    });

})();

(function() {
  'use strict';

  angular
    .module('demoApp')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($mdSidenav, $timeout, $rootScope, $scope, $state) {
    var vm = this;

    vm.demoMenuItems = [{
      label: 'Introduction',
      stateName: 'main.intro'
    }, {
      label: 'Number',
      stateName: 'main.number'
    }, {
      label: 'Date',
      stateName: 'main.date'
    }, {
      label: 'Currency',
      subLabel: 'Cases',
      stateName: 'main.currency.case'
    }, {
      label: 'Currency',
      subLabel: 'CPUs',
      stateName: 'main.currency.cpu',
      hide: true
    }, {
      label: 'Currency',
      subLabel: 'Hard-Drives',
      stateName: 'main.currency.storage',
      hide: true
    }, {
      label: 'Currency',
      subLabel: 'Memory',
      stateName: 'main.currency.mem',
      hide: true
    }, {
      label: 'Currency',
      subLabel: 'Cart',
      stateName: 'main.currency.cart',
      hide: true
    }];
    vm.demoMenuInx = 0;

    var getLabel = function() {
      angular.forEach(vm.demoMenuItems, function(item, inx) {
        if (item.stateName === $state.current.name) {
          vm.currentLabel = item.label;
          vm.currentSubLabel = item.subLabel;
          vm.demoMenuInx = inx;
          return true;
        }
      });
    };
    getLabel();

    vm.openNav = function() {
      $timeout(function() {
        $mdSidenav('left').open();
      });
    };

    var locationChangeSuccessEvent = $rootScope.$on('$locationChangeSuccess', function() {
      getLabel();
      $timeout(function() {
        $mdSidenav('left').close();
      });
    });

    $scope.$on('$destroy', function() {
      if (locationChangeSuccessEvent) {
        locationChangeSuccessEvent();
      }
    });

  }

})();

