$templateCache.put("app/main/views/date/date.html","<md-content layout-padding><p>Use <b>ngTextRoll</b> to bring attention to date changes:</p><p id=date-demo>Lorem ipsum dolor sit amet, pellentesque sed libero, orci nec massa netus, &nbsp;<ng-text-roll target=date.theValue config=date.rollConfig></ng-text-roll>dolor est augue nec odio sit, quo in enim arcu pede ultrices. Vitae cras inventore urna dapibus.</p><div layout=row layout-xs=column><md-input-container><label>Select Operation</label><md-select ng-model=date.chosenOperation><md-option ng-repeat=\"op in date.operations\" value={{op.op}}>{{op.label}}</md-option></md-select></md-input-container><md-input-container><label>Enter a value</label><input type=number ng-model=date.delta></md-input-container><div flex><md-button class=\"md-raised md-primary\" ng-click=date.changeValue() ng-disabled=\"!date.chosenOperation || !date.delta\">Change Value</md-button><md-button class=\"md-raised md-primary\" ng-click=date.resetValue()>Reset Value</md-button></div></div><div layout=row layout-xs=column class=roll-chars><md-input-container><md-switch ng-model=date.rollConfig.rollAll>Rolling {{date.rollConfig.rollAll ? \"all\" : \"only changed\"}} characters</md-switch></md-input-container><md-input-container><md-switch ng-model=date.rollConfig.rollBetween>Rolling {{date.rollConfig.rollBetween ? \"all characters between changes\" : \"only character differences\"}}</md-switch></md-input-container></div><div layout=row layout-xs=column><md-input-container><label>Enter a date format</label><input type=text ng-model=date.rollConfig.filterParam1 ng-change=date.triggerChange()></md-input-container></div></md-content>");
$templateCache.put("app/main/views/intro/intro.html","<md-content layout-padding><h2>What is ngTextRoll?</h2><p>With today\'s very dynamic web experience, elements and text on a page can change frequently. &nbsp;&nbsp;<b>ngTextRoll</b> is an <a href=https://angularjs.org/ target=_blank>AngularJS</a> <a href=https://docs.angularjs.org/guide/component target=_blank>component</a> designed to help draw attention to these types of changes.</p><div class=\"layout-align-center layout-row\"><ng-text-roll class=intro-demo target=intro.theValue></ng-text-roll></div><h2>Installation</h2><section><ol><li>Install via <a href=http://bower.io/ target=_blank>Bower</a></li><div hljs hljs-no-escape>>$ bower install ng-text-roll</div><li>Add the ngTextRoll module</li><div hljs>angular.module(\'myApp\', [\'ui.ngTextRoll\'])</div><li>Use the component</li>\n      <p>\n        Markup:\n        <div hljs hljs-language=\"HTML\"><ng-text-roll target=\"theValue\"></ng-text-roll></div>\n      </p>\n       JS:\n      <div hljs hljs-language=\"JavaScript\">angular\n  .module(\'demoApp\')\n    .controller(\'IntroController\', function() {\n      var vm = this;\n      this.theValue = 123;\n    });</div>\n      </ol></section><p>Check out the <a href=https://github.com/daveteply/ng-text-roll>GitHub</a> page for usage and available options.</p></md-content>");
$templateCache.put("app/main/views/number/number.html","<md-content layout-padding><section><p><b>ngTextRoll</b> can be embedded in a paragrah of text. Take the following example:</p><p>Lorem ipsum dolor sit amet, pellentesque sed libero, orci nec massa netus, dolor est augue nec odio sit, quo in enim arcu pede ultrices. Vitae cras inventore urna dapibus. Orci amet &nbsp;<ng-text-roll target=number.theValue></ng-text-roll>nullam vitae, urna curabitur lorem etiam tristique leo, ipsum dignissim magna. Sollicitudin mollis et dictum sed, quam at fusce dui sit sed et. Vitae integer nisl malesuada ullamcorper tincidunt, non aliquet bibendum. Metus netus, dignissim diam.</p><p>Now change the value: <button style=vertical-align:bottom md-ink-ripple=#3F51B5 class=\"md-button md-raised md-ink-ripple\" type=button ng-click=number.changeTheValue()><span>Change Value</span></button> Really catches your eye, eh? :) You may notice sometimes there\'s a slight blurring effect when the text changes. This occurs when the over length of the string changes to compensate for propotional fonts.</p></section><md-divider></md-divider><section><p>Here\'s another example:<ng-text-roll class=md-display-1 target=number.theOtherValue config=number.rollConfig></ng-text-roll></p><div layout=row layout-xs=column><md-input-container><label>Select Operation</label><md-select ng-model=number.chosenOperation><md-option ng-repeat=\"op in number.operations\" value={{op.op}}>{{op.label}}</md-option></md-select></md-input-container><md-input-container><label>Enter a value</label><input type=number ng-model=number.delta></md-input-container><div flex><md-button class=\"md-raised md-primary\" ng-click=number.changeValue() ng-disabled=\"!number.chosenOperation || !number.delta\">Change Value</md-button><md-button class=\"md-raised md-primary\" ng-click=\"number.theOtherValue = 12345.67\">Reset Value</md-button></div></div><div layout=row layout-xs=column class=roll-chars><md-input-container><md-switch ng-model=number.rollConfig.rollAll>Rolling {{number.rollConfig.rollAll ? \"all\" : \"only changed\"}} characters</md-switch></md-input-container><md-input-container><md-switch ng-model=number.rollConfig.rollBetween>Rolling {{number.rollConfig.rollBetween ? \"all characters between changes\" : \"only character differences\"}}</md-switch></md-input-container></div></section></md-content>");
